// Fullscreen editor styles
// Scoped to .cfw-checkout-editor-page body class

body.cfw-checkout-editor-page {
    #wpadminbar,
    #adminmenumain,
    #adminmenuwrap,
    #wpfooter {
        display: none !important;
    }

    #wpcontent {
        margin-left: 0 !important;
        padding-left: 0 !important;
    }

    .cfw-admin-notices-container {
        display: none;
    }
}

.cfw-editor {
    display: flex;
    flex-direction: column;
    height: 100vh;
    overflow: hidden;

    &__header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 56px;
        min-height: 56px;
        padding: 0 16px;
        background: #fff;
        border-bottom: 1px solid #e5e7eb;
        z-index: 100;

        &-left {
            display: flex;
            align-items: center;
            gap: 12px;
        }

        &-center {
            display: flex;
            align-items: center;
            gap: 12px;
        }

        &-right {
            display: flex;
            align-items: center;
            gap: 12px;
        }
    }

    &__close-btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 36px;
        height: 36px;
        border: none;
        background: none;
        border-radius: 6px;
        cursor: pointer;
        color: #6b7280;
        transition: background-color 0.15s, color 0.15s;

        &:hover {
            background-color: #f3f4f6;
            color: #111827;
        }

        svg {
            width: 20px;
            height: 20px;
        }
    }

    &__title {
        font-size: 14px;
        font-weight: 600;
        color: #111827;
        margin: 0;
    }

    &__unsaved {
        display: flex;
        align-items: center;
        gap: 6px;
        font-size: 13px;
        color: #ea580c;

        &-dot {
            width: 8px;
            height: 8px;
            border-radius: 50%;
            background: #ea580c;
        }
    }

    &__device-toggle {
        display: flex;
        align-items: center;
        gap: 2px;
        padding: 4px;
        background: #f3f4f6;
        border-radius: 8px;

        button {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 32px;
            height: 32px;
            border: none;
            background: none;
            border-radius: 6px;
            cursor: pointer;
            color: #6b7280;
            transition: background-color 0.15s, color 0.15s;

            &:hover {
                color: #374151;
            }

            &.active {
                background: #fff;
                color: #111827;
                box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
            }

            svg {
                width: 18px;
                height: 18px;
            }
        }
    }

    &__save-btn {
        display: inline-flex;
        align-items: center;
        padding: 8px 16px;
        border: none;
        border-radius: 9999px;
        font-size: 13px;
        font-weight: 600;
        color: #fff;
        background: #2563eb;
        cursor: pointer;
        transition: background-color 0.15s, opacity 0.15s;

        &:hover:not(:disabled) {
            background: #1d4ed8;
        }

        &:disabled {
            opacity: 0.5;
            cursor: not-allowed;
        }
    }

    &__body {
        display: flex;
        flex: 1;
        overflow: hidden;
    }

    &__sidebar {
        width: 360px;
        min-width: 360px;
        background: #fff;
        border-right: 1px solid #e5e7eb;
        display: flex;
        flex-direction: column;
        overflow: hidden;

        &-tabs {
            display: flex;
            border-bottom: 1px solid #e5e7eb;
            padding: 0 16px;

            button {
                flex: 1;
                padding: 12px 0;
                border: none;
                background: none;
                font-size: 13px;
                font-weight: 500;
                color: #6b7280;
                cursor: pointer;
                border-bottom: 2px solid transparent;
                transition: color 0.15s, border-color 0.15s;

                &:hover {
                    color: #374151;
                }

                &.active {
                    color: #2563eb;
                    border-bottom-color: #2563eb;
                }
            }
        }

        &-search {
            padding: 12px 16px;
            border-bottom: 1px solid #e5e7eb;

            input {
                display: block;
                width: 100%;
                padding: 8px 12px 8px 32px;
                font-size: 13px;
                border: 1px solid #d1d5db;
                border-radius: 6px;
                background: #f9fafb;
                outline: none;
                transition: border-color 0.15s;

                &:focus {
                    border-color: #2563eb;
                }
            }

            .search-icon {
                position: absolute;
                left: 24px;
                top: 50%;
                transform: translateY(-50%);
                width: 16px;
                height: 16px;
                color: #9ca3af;
                pointer-events: none;
            }
        }

        &-content {
            flex: 1;
            overflow-y: auto;
            padding: 0;
        }
    }

    &__preview {
        flex: 1;
        background: #f3f4f6;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 24px;
        position: relative;
        overflow: hidden;

        &-frame {
            background: #fff;
            border-radius: 8px;
            box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
            overflow: hidden;
            transition: width 0.3s ease;
            height: 100%;
            position: relative;
        }

        iframe {
            width: 100%;
            height: 100%;
            border: none;
            display: block;
        }

        &-loading {
            position: absolute;
            inset: 0;
            display: flex;
            align-items: center;
            justify-content: center;
            background: rgba(255, 255, 255, 0.8);
            z-index: 10;
        }

        &-no-products {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            gap: 12px;
            padding: 32px;
            text-align: center;
            color: #6b7280;

            svg {
                width: 48px;
                height: 48px;
                color: #d1d5db;
            }

            p {
                font-size: 14px;
                margin: 0;
            }
        }
    }
}

// EditorSection accordion
.cfw-editor-section {
    border-bottom: 1px solid #e5e7eb;

    &__header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 14px 16px;
        cursor: pointer;
        user-select: none;
        transition: background-color 0.15s;

        &:hover {
            background-color: #f9fafb;
        }

        h3 {
            font-size: 13px;
            font-weight: 600;
            color: #111827;
            margin: 0;
        }

        svg {
            width: 16px;
            height: 16px;
            color: #9ca3af;
            transition: transform 0.2s;
        }

        &.open svg {
            transform: rotate(180deg);
        }
    }

    &__body {
        padding: 0 16px 16px;

        .cfw-admin-field-container {
            margin-bottom: 12px;

            &:last-child {
                margin-bottom: 0;
            }
        }

        // Make color pickers fit in sidebar
        .cfw-admin-field-container.mb-4.w-min {
            width: 100% !important;
        }

        // Compact admin page section in sidebar
        .md\:grid {
            display: block !important;
        }

        .md\:col-span-1 {
            display: none;
        }

        .md\:col-span-2 {
            margin-top: 0;
        }
    }
}

// Spinner
.cfw-editor-spinner {
    width: 40px;
    height: 40px;
    border: 3px solid #e5e7eb;
    border-top-color: #2563eb;
    border-radius: 50%;
    animation: cfw-editor-spin 0.8s linear infinite;
}

@keyframes cfw-editor-spin {
    to {
        transform: rotate(360deg);
    }
}
